<template>
    <div class="p-20">

        <div style="margin-left: 10px;margin-bottom: 20px">
            <el-button type="primary" @click="showAddDialog" v-if="permission.add" size="small">新增</el-button>
        </div>

        <el-table
                ref="singleTable"
                :data="tableData"
                style="width: 100%">
            <el-table-column type="index" label="序号" width="80"></el-table-column>
            <el-table-column
                    v-for="item in tableHeader"
                    :key="item.prop"
                    :label="item.label"
                    :prop="item.prop"
                    :width="item.width"
                    :min-width="item.minWidth"
                    :align="item.align"
                    :formatter="item.formatter"
                    show-overflow-tooltip>
            </el-table-column>
            <el-table-column
                    fixed="right"
                    label="操作"
                    width="160">
                <template scope="scope" class="filter-operation">
                    <ul style="list-style: none;padding: 0px">
                        <li style="float:left;width: 30px">
                            <el-button
                                    type="text" size="small"
                                    @click="getCategory(scope.row.id)" v-if="permission.edit">修改
                            </el-button>
                        </li>
                        <li style="float:left;width: 30px" v-if="scope.row.status===1">
                            <el-button
                                    type="text" size="small"
                                    @click="showDeleteDialog(scope.row.id,'启用')" v-if="permission.enable">启用
                            </el-button>
                        </li>
                        <li style="float:left;width: 30px" v-if="scope.row.status!==1">
                            <el-button
                                    type="text" size="small"
                                    @click="showDeleteDialog(scope.row.id,'禁用')" v-if="permission.disable">禁用
                            </el-button>
                        </li>
                        <li style="float:left;width: 30px">
                            <el-button
                                    type="text" size="small"
                                    @click="showFeesDialog(scope.row.id)" v-if="permission.edit">修改手续费
                            </el-button>
                        </li>
                    </ul>
                </template>
            </el-table-column>
        </el-table>

        <el-dialog title="新增实体币" :visible.sync="dialogVisible" @close="dialogClose">
            <el-form :model="data" :rules="rules" ref="data">
                <el-form-item :label-width="itemWidth" label="*图标" prop="iconUrl">
                    <el-upload
                            class="upload-demo"
                            action="/upload/nofilter/file"
                            list-type="picture"
                            :on-success="handleSuccess"
                            :on-remove="handleRemove"
                            name="uploadFile"
                            :before-upload="handleBeforeUpload"
                            :autoUpload="true"
                            :data="uploadData"
                            :file-list="fileList">
                        <el-button size="small" type="primary">上传图标</el-button>
                        <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，512px*512px,且不超过2M</div>
                    </el-upload>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="*中文名称" prop="name">
                    <el-input class="input-short" v-model="data.name"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="*简称" prop="shortName">
                    <el-input class="input-short" v-model="data.shortName"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="*英文名称" prop="englishName">
                    <el-input class="input-short" v-model="data.englishName"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="*是否可以充值" prop="rechargeStatus">
                    <el-switch
                            v-model="data.rechargeStatus"
                            on-text=""
                            off-text=""
                            :on-value="2"
                            :off-value="1">
                    </el-switch>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="*是否可以提现" prop="withdrawStatus">
                    <el-switch
                            v-model="data.withdrawStatus"
                            on-text=""
                            off-text=""
                            :on-value="2"
                            :off-value="1">
                    </el-switch>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="*每次最小交易金额" prop="minWithdrawAmt">
                    <el-input class="input-short" v-model="data.minWithdrawAmt"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="*每次最大交易金额" prop="maxWithdrawAmt">
                    <el-input class="input-short" v-model="data.maxWithdrawAmt"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="*每天最大提现金额" prop="maxWithdrawAmtDaily">
                    <el-input class="input-short" v-model="data.maxWithdrawAmtDaily"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="*每天最大提现金额" prop="maxWithdrawNumDaily">
                    <el-input class="input-short" v-model="data.maxWithdrawNumDaily"
                              auto-complete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="validate">保 存</el-button>
            </div>
        </el-dialog>

        <el-dialog title="修改手续费率" :visible.sync="feesDialogVisible" @close="feesDialogClose">
            <span style="color: #FF0000;position:absolute;top:22px;left:140px;">0.01 = 1%</span>
            <el-form :model="fees" :rules="ruleFees" ref="fees">
                <el-form-item :label-width="itemWidth" label="等级[ 1 ]提现手续费：" prop="fetchOne">
                    <el-input class="input-fee" v-model="fees.fetchOne"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="等级[ 2 ]提现手续费：" prop="fetchTwo">
                    <el-input class="input-fee" v-model="fees.fetchTwo"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="等级[ 3 ]提现手续费：" prop="fetchThree">
                    <el-input class="input-fee" v-model="fees.fetchThree"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="等级[ 4 ]提现手续费：" prop="fetchFour">
                    <el-input class="input-fee" v-model="fees.fetchFour"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="等级[ 5 ]提现手续费：" prop="fetchFive">
                    <el-input class="input-fee" v-model="fees.fetchFive"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="等级[ 6 ]提现手续费：" prop="fetchSix">
                    <el-input class="input-fee" v-model="fees.fetchSix"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="等级[ 7 ]提现手续费：" prop="fetchSeven">
                    <el-input class="input-fee" v-model="fees.fetchSeven"
                              auto-complete="off"></el-input>
                </el-form-item>
            </el-form>

            <div slot="footer" class="dialog-footer">
                <el-button @click="feesDialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="modifyFees">保 存</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>

    import fetch from '@/utils/fetch';

    import DCTP from '@/utils/dctp';

    import {validateEightDecimal} from '@/utils/validate';

    export default {
        data() {
            let validate = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('不能为空'));
                }
                callback();
            };
            let validateNumber = (rule, value, callback) => {
                if (value === '')
                    callback(new Error('请输入数量'));
                if (!validateEightDecimal(value) || value * 1 >= 9999999 || value <= 0)
                    callback(new Error('输入不合法'));
                callback();
            };
            let validateFees = (rule, value, callback) => {
                if (value === '' || !validateEightDecimal(value)) {
                    callback(new Error('费率输入不合法'));
                }
                callback();
            };
            return {
                itemWidth: '160px',
                dialogVisible: false,
                feesDialogVisible: false,
                data: {
                    id: '',
                    iconUrl: '',
                    iconFullUrl: '',
                    name: '',
                    shortName: '',
                    englishName: '',
                    status: 0,
                    rechargeStatus: '',
                    withdrawStatus: '',
                    minWithdrawAmt: '',
                    maxWithdrawAmt: '',
                    maxWithdrawAmtDaily: '',
                    maxWithdrawNumDaily: '',
                    type: '3'
                },
                action: '',
                rules: {
                    name: [{validator: validate, trigger: 'blur'}],
                    shortName: [{validator: validate, trigger: 'blur'}],
                    englishName: [{validator: validate, trigger: 'blur'}],
                    sendCoinId: [{validator: validate, trigger: 'blur'}],
                    minWithdrawAmt: [{validator: validateNumber, trigger: 'blur'}],
                    maxWithdrawAmt: [{validator: validateNumber, trigger: 'blur'}],
                    maxWithdrawAmtDaily: [{validator: validateNumber, trigger: 'blur'}],
                    maxWithdrawNumDaily: [{validator: validateNumber, trigger: 'blur'}]
                },
                fees: {
                    baseSeq: '',
                    procedureSeq: '',
                    fetchOne: '',
                    fetchTwo: '',
                    fetchThree: '',
                    fetchFour: '',
                    fetchFive: '',
                    fetchSix: '',
                    fetchSeven: ''
                },
                ruleFees: {
                    fetchOne: [{validator: validateFees, trigger: 'blur'}],
                    fetchTwo: [{validator: validateFees, trigger: 'blur'}],
                    fetchThree: [{validator: validateFees, trigger: 'blur'}],
                    fetchFour: [{validator: validateFees, trigger: 'blur'}],
                    fetchFive: [{validator: validateFees, trigger: 'blur'}],
                    fetchSix: [{validator: validateFees, trigger: 'blur'}],
                    fetchSeven: [{validator: validateFees, trigger: 'blur'}]
                },
                tableData: [],
                tableHeader: [{
                    label: "FID",
                    prop: "id",
                    width: 120
                }, {
                    label: "中文名称",
                    prop: "name",
                    width: 120
                }, {
                    label: "简称",
                    prop: "shortName",
                    width: 120
                }, {
                    label: "英文名称",
                    prop: "englishName",
                    width: 120
                }, {
                    label: "状态",
                    prop: "status",
                    width: 80,
                    formatter(row, column, value) {
                        return value === 1 ? '禁用' : (value === 2 ? '启用' : '');
                    }
                }, {
                    label: "是否可以充值",
                    prop: "rechargeStatus",
                    width: 120,
                    formatter(row, column, value) {
                        return value === 1 ? '否' : (value === 2 ? '是' : '');
                    }
                }, {
                    label: "是否可以提现",
                    prop: "withdrawStatus",
                    width: 120,
                    formatter(row, column, value) {
                        return value === 1 ? '否' : (value === 2 ? '是' : '');
                    }
                }, {
                    label: "每次最小提现金额",
                    prop: "minWithdrawAmt",
                    width: 180
                }, {
                    label: "每次最大提现金额",
                    prop: "maxWithdrawAmt",
                    width: 180
                }, {
                    label: "每天最大提现金额",
                    prop: "maxWithdrawAmtDaily",
                    width: 180
                }, {
                    label: "每天最大提现金额",
                    prop: "maxWithdrawNumDaily",
                    width: 180
                }],
                fileList: [],
                virtualList: [],
                uploadData: {
                    type: 1,
                    usageType: 3
                }
            }
        },
        created() {
            this.getList();
        },
        methods: {
            addCategory() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/entitymoney',
                        method: 'post',
                        data: _this.data
                    }).then(function (response) {
                        if (response.result) {
                            _this.dialogVisible = false;
                            _this.getList();
                        }
                        _this.getMessage(response.msg, response.result);
                    }).catch();
                })
            },
            showAddDialog() {
                this.dialogVisible = true;
                this.action = 'add';
            },
            validate() {
                this.$refs['data'].validate((valid) => {
                    if (valid) {
                        switch (this.action) {
                            case 'add':
                                this.addCategory();
                                break;
                            default:
                                this.modify();
                                break;
                        }
                    }
                });
            },
            dialogClose() {
                this.resetData();
            },
            setData(data) {
                let key;
                for (key in this.data) {
                    this.data[key] = data[key] === null ? '' : data[key];
                }
                this.fileList = [{name: '当前图片', url: data.iconFullUrl}];
            },
            resetData() {
                let key;
                this.$refs['data'].resetFields();
                for (key in this.data) {
                    switch (key) {
                        case 'type':
                            this.data[key] = '3';
                            break;
                        default:
                            this.data[key] = '';
                    }
                }
                this.action = '';
                this.fileList = [];
            },
            setFeeData(data) {
                let key;
                for (key in this.fees) {
                    this.fees[key] = data[key] === null ? '' : data[key];
                }
            },
            feesDialogClose() {
                this.resetFees();
            },
            resetFees() {
                let key;
                for (key in this.fees) {
                    switch (key) {
                        default:
                            this.fees[key] = '';
                    }
                }
            },
            getList() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/entitymoney',
                        method: 'get',
                        params: _this.queryData
                    }).then(function (response) {
                        if (response.result) {
                            _this.tableData = response.data;
                        }
                    }).catch();
                })
            },
            getCategory(id) {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/entitymoney/' + id,
                        method: 'get'
                    }).then(function (response) {
                        if (response.result) {
                            _this.setData(response.data);
                            _this.dialogVisible = true;
                        }
                    }).catch();
                })
            },
            showDeleteDialog(id, str) {
                this.$confirm('确认' + str + '？', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    switch (str) {
                        case '启用':
                            this.enable(id);
                            break;
                        case '禁用':
                            this.disable(id);
                            break;
                    }
                }).catch(() => {
                    this.$message.info('操作取消');
                });
            },
            showFeesDialog(id) {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: '/admin/entitymoney/' + id + '/fees',
                        method: 'get'
                    }).then(function (response) {
                        if (response.result) {
                            if (response.data) {
                                _this.setFeeData(response.data);
                            }
                            _this.fees.baseSeq = id;
                            _this.feesDialogVisible = true;
                        }
                    }).catch();
                });
            },
            modify() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/entitymoney',
                        method: 'put',
                        data: _this.data
                    }).then(function (response) {
                        if (response.result) {
                            _this.dialogVisible = false;
                            _this.getList();
                        }
                        _this.getMessage(response.msg, response.result);
                    }).catch();
                })
            },
            query() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/entitymoney/query',
                        method: 'get',
                        params: _this.queryData
                    }).then(function (response) {
                        if (response.result) {
                            _this.tableData = response.data.data;
                            _this.total = response.data.page.count;
                        }
                    }).catch();
                })
            },
            modifyFees() {
                let _this = this;
                this.$refs['fees'].validate((valid) => {
                    if (valid) {
                        return new Promise((resolve, reject) => {
                            fetch({
                                url: 'admin/entitymoney/' + _this.fees.baseSeq + '/fees',
                                method: 'put',
                                data: _this.fees
                            }).then(function (response) {
                                if (response.result) {
                                    _this.feesDialogVisible = false;
                                }
                                _this.getMessage(response.msg, response.result);
                            }).catch();
                        });
                    }
                });
            },
            enable(id) {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/entitymoney/' + id + '/changestatus',
                        method: 'put',
                        params: {status: 2}
                    }).then(function (response) {
                        if (response.result) {
                            _this.getList();
                        }
                        _this.getMessage(response.msg, response.result);
                    }).catch();
                })
            },
            disable(id) {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/entitymoney/' + id + '/changestatus',
                        method: 'put',
                        params: {status: 1}
                    }).then(function (response) {
                        if (response.result) {
                            _this.getList();
                        }
                        _this.getMessage(response.msg, response.result);
                    }).catch();
                })
            },
            handleRemove(file, fileList) {
                this.data.iconUrl = '';
            },
            handleSuccess(response, file, fileList) {
                this.fileList = [fileList[fileList.length - 1]];
                this.data.iconUrl = response.data;
            },
            handleBeforeUpload(file) {
                let filename = file.name.toLowerCase();
                if (!filename.endsWith('.jpg') && !filename.endsWith('.jpeg') && !filename.endsWith('.png')) {
                    this.$message.error('格式不对，请重新选择');
                    return false;
                }
                if (file.size > 1024 * 1024 * 2) {
                    this.$message.error('图片太大，请重新选择');
                    return false;
                }
                return true;
            }
        }
    }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
    .input-short {
        width: 300px;
    }

    .input-fee {
        width: 90px;
    }
</style>


